import Content from "@/components/content";
import { links } from "@/common/local-link";
import { HomeWrapper } from "./style";
import Bear from "@/components/bear";

import React, { memo, useState } from "react";

import { Input, Radio, Tag } from "antd";
import { styled } from "styled-components";
const { Search } = Input;

const Home = memo(() => {
  const [radioValue, setRadioValue] = useState(1);

  const onSearch = (word) => {
    let url = "https://www.baidu.com";
    if (radioValue === 1) {
      url = "https://www.baidu.com/s?wd=" + word;
    } else {
      url = "https://www.google.com/search?q=" + word;
    }
    window.open(url, "_blank");
  };

  const onChange = (e) => {
    setRadioValue(e.target.value);
  };

  const toLink = (url) => {
    window.open(url, "_blank");
  };

  return (
    <HomeWrapper>
      <Bear className="bear"></Bear>

      <Content className="search">
        <h6>搜索</h6>
        <Search placeholder="请输入搜索词" onSearch={onSearch} enterButton />
        <Radio.Group
          className="search-type"
          onChange={onChange}
          name="radiogroup"
          defaultValue={1}
        >
          <Radio value={1}>Baidu</Radio>
          <Radio value={2}>Google</Radio>
        </Radio.Group>
      </Content>

      <Content>
        <h6>快捷入口</h6>
        <div className="links-pane">
          {links.map((item, index) => {
            return (
              <Tag
                onClick={(e) => {
                  toLink(item.link);
                }}
                color="cyan"
                key={item.title}
                className="links-item"
              >
                {item.title}
              </Tag>
            );
          })}
        </div>
      </Content>
    </HomeWrapper>
  );
});

export default Home;
